@charset "utf-8";

:root {
  --active-file-bg-color: #dadada;
  --active-file-bg-color: rgba(32, 43, 51, 0.63);
  --active-file-text-color: white;
  --bg-color: #fff;
  --text-color: #333;
  --side-bar-bg-color: #f5f5f5;
  --control-text-color: #666;
}

/* Èò≤Ê≠¢Áî®Êà∑Ëá™ÂÆö‰πâËÉåÊôØÈ¢úËâ≤ÂØπÁΩëÈ°µÁöÑÂΩ±ÂìçÔºåÊ∑ªÂä†ËÆ©Áî®Êà∑ÂèØ‰ª•Ëá™ÂÆö‰πâÂ≠ó‰Ωì */
html {
  color: #333;
  background: #fff;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  text-rendering: optimizelegibility;
  font-size: 14px;
  -webkit-font-smoothing: initial;
}

#write {
  max-width: 960px;
  padding-top: 2em;
  padding-left: 60px;
  padding-right: 60px;
  min-height: calc(100vh - 6em);
  -webkit-font-smoothing: antialiased;
  font-size: 16px;
}

.typora-node #write {
  min-height: calc(100% - 6em);
}

pre.md-meta-block {
  background: #f5f5f5;
  padding: 1em;
  border-radius: 3px;
  font-size: 14px;
}

@media screen and (max-width: 800px) {
  html {
    font-size: 14px;
  }

  #write {
    padding-left: 30px;
    padding-right: 30px;
    font-size: 14px;
  }
}

@media screen and (min-width: 1100px) {
  body,
  #footer-word-count-info {
    background: #f5f5f5;
  }

  body.pin-outline,
  .pin-outline #footer-word-count-info,
  .pin-outline footer {
    background: #fff;
  }

  #write {
    max-width: 1000px;
    padding: 40px 60px;
    background: #fff;
    margin: 3em auto 3em;
    border: 1px solid #ddd;
    border-width: 0 1px;
  }

  .pin-outline #write {
    max-width: 1000px;
    background: #fff;
    margin: 0 0 0;
    border: 0;
    padding-left: 60px;
    padding-right: 60px;
  }

  footer {
    background-color: transparent;
  }
}

@media screen and (min-width: 1300px) {
  body.pin-outline,
  .pin-outline #footer-word-count-info,
  .pin-outline footer {
    background: #f5f5f5;
  }

  .pin-outline #write {
    max-width: 1000px;
    padding: 40px 60px;
    background: #fff;
    margin: 3em auto 3em;
    border: 1px solid #ddd;
    border-width: 0 1px;
  }

  .pin-outline footer {
    background-color: transparent;
  }

  #footer-word-count-info {
    background: #f5f5f5;
  }
}

/* Â¶ÇÊûú‰Ω†ÁöÑÈ°πÁõÆ‰ªÖÊîØÊåÅ IE9+ | Chrome | Firefox Á≠âÔºåÊé®ËçêÂú® <html> ‰∏≠Ê∑ªÂä† .borderbox Ëøô‰∏™ class */
html.borderbox *,
html.borderbox *:before,
html.borderbox *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

/* ÂÜÖÂ§ñËæπË∑ùÈÄöÂ∏∏ËÆ©ÂêÑ‰∏™ÊµèËßàÂô®Ê†∑ÂºèÁöÑË°®Áé∞‰ΩçÁΩÆ‰∏çÂêå */
body,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
code,
form,
fieldset,
legend,
input,
textarea,
p,
blockquote,
th,
td,
hr,
button,
article,
aside,
details,
figcaption,
figure,
footer,
header,
menu,
nav,
section {
  margin: 0;
  padding: 0;
}

/* ÈáçËÆæ HTML5 Ê†áÁ≠æ, IE ÈúÄË¶ÅÂú® js ‰∏≠ createElement(TAG) */
article,
aside,
details,
figcaption,
figure,
footer,
header,
menu,
nav,
section {
  display: block;
}

/* HTML5 Â™í‰ΩìÊñá‰ª∂Ë∑ü img ‰øùÊåÅ‰∏ÄËá¥ */
audio,
canvas,
video {
  display: inline-block;
}

/* Ë¶ÅÊ≥®ÊÑèË°®ÂçïÂÖÉÁ¥†Âπ∂‰∏çÁªßÊâøÁà∂Á∫ß font ÁöÑÈóÆÈ¢ò */
body,
button,
input,
select,
textarea {
  font: 300 1em/1.8 'PingFang SC', 'Lantinghei SC', 'Microsoft Yahei', 'Hiragino Sans GB', 'Microsoft Sans Serif', 'WenQuanYi Micro Hei',
    sans;
}

